.card {
  background: #000073;
  border-radius: 2px;
  height: 250px;
  margin: 1rem;
  position: relative;
  width: 325px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
	background:black;
	border-style: solid;
	border-color: #999;
}
.card > .img-container > img {
  width: 90%
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}


.wrapper {
  padding-top: 50px;
  background: #000073;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}
.header {
  width: 100%;
  font-size: 2rem;
  color: #2A77AE;
  margin-bottom: 10px;
}

.title {
  float: left;
}

.scores {
  float: right;
}


body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
	font-family: 'Press Start 2P', cursive;
	font-size: 80%;
	color: yellow;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

